.App {
  text-align: center;
}

body {
  background-color: #3b3b3b;
}

.App-header {
  font-size: 2rem;
  letter-spacing: 1px;
  /*background: linear-gradient(to right,#FFB669,#FF7C36);*/
  background: linear-gradient(to right,#E67E22,#D35400);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

table {
  width: 700px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  display: block;
  border: 1px solid black;
  height: 492px;
  overflow-y: scroll;
  justify-content: center;
  align-Items: 'center';
  margin: auto auto;
}

th,
td {
  padding: 10px;
  border: 0.5px;
  background-color: rgba(255, 255, 255, 0.3);
  color: #fff;
  font-display: 'black';
}
th {
  text-align: center;
  scroll-snap-stop: always;
  position: sticky;
  top: 0;
}
thead th {
  background-color: #55608f;
}
tbody tr:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
tbody td {
  position: relative;
}
tbody td:hover:before {
  content: "";
  position: relative;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.2);
  z-index: -1;
}
